import { LitElement, html } from '@polymer/lit-element';
import { HAXWiring } from "@lrnwebcomponents/hax-body-behaviors/lib/HAXWiring.js"
/**
 * `micro-copy-heading`
 * `small call to action / attention that acts as a heading too`
 *
 * @microcopy - language worth noting:
 *  -
 *
 * @customElement
 * @lit-html
 * @lit-element
 * @demo demo/index.html
 */
class MicroCopyHeading extends LitElement {
  
  // render function
  render() {
    return html`
<style>:host {
  display: block;
}

:host([hidden]) {
  display: none;
}
</style>
<slot></slot>
<div>${this.heading}</div>
<div>${this.endCap}</div>`;
  }

  // haxProperty definition
  static get haxProperties() {
    return {
  "canScale": true,
  "canPosition": true,
  "canEditSource": false,
  "gizmo": {
    "title": "Micro copy-heading",
    "description": "small call to action / attention that acts as a heading too",
    "icon": "icons:android",
    "color": "green",
    "groups": [
      "Copy"
    ],
    "handles": [
      {
        "type": "todo:read-the-docs-for-usage"
      }
    ],
    "meta": {
      "author": "btopro",
      "owner": "The Pennsylvania State University"
    }
  },
  "settings": {
    "quick": [],
    "configure": [
      {
        "property": "heading",
        "description": "",
        "inputMethod": "textfield",
        "required": false,
        "icon": "icons:android"
      },
      {
        "property": "endCap",
        "description": "",
        "inputMethod": "textfield",
        "required": false,
        "icon": "icons:android"
      }
    ],
    "advanced": []
  }
};
  }
  // properties available to the custom element for data binding
  static get properties() {
    return {
  "heading": {
    "name": "heading",
    "type": "String",
    "value": "Tell your story",
    "reflectToAttribute": false,
    "observer": false
  },
  "endCap": {
    "name": "endCap",
    "type": "String",
    "value": "//",
    "reflectToAttribute": false,
    "observer": false
  }
};
  }

  /**
   * Store the tag name to make it easier to obtain directly.
   * @notice function name must be here for tooling to operate correctly
   */
  tag() {
    return "micro-copy-heading";
  }

  // life cycle
  constructor() {
    super();
    
  }
  /**
   * life cycle, element is afixed to the DOM
   */
  connectedCallback() {
    super.connectedCallback();
    this.HAXWiring = new HAXWiring();
    this.HAXWiring.setup(MicroCopyHeading.haxProperties, MicroCopyHeading.tag, this);
  }
  // static get observedAttributes() {
  //   return [];
  // }
  // disconnectedCallback() {}

  // attributeChangedCallback(attr, oldValue, newValue) {}
  
}
customElements.define("micro-copy-heading", MicroCopyHeading);
export { MicroCopyHeading };
